import mongoose from 'mongoose';

// Device Schema
const deviceSchema = new mongoose.Schema({
  Id: { type: Number, required: true },
  Created: { type: Date, required: true },
  Updated: { type: Date, required: true },
  DeviceType: { type: String, required: true },
  Description: { type: String },
  IpAddress: { type: String },
  Status: { type: Boolean, default: false },
  LastStatusWhen: { type: Date },
  IsEnabled: { type: Boolean, default: true },
}, { _id: false });

// Platform Schema
const platformSchema = new mongoose.Schema({
  PlatformNumber: { type: String, required: true },
  PlatformType: { type: String },
  Description: { type: String },
  Subnet: { type: String },
  Devices: { type: [deviceSchema], default: [] }
}, { _id: false });

// Main Schema
const platformDeviceSchema = new mongoose.Schema({
  stationCode: { type: String, required: true, unique: true },
  stationName: { type: String, required: true },
  platforms: { type: [platformSchema], default: [] }
}, { timestamps: true });

const PlatformDevice = mongoose.models.PlatformDevice || mongoose.model('PlatformDevice', platformDeviceSchema);

export default PlatformDevice;